<template>
  <div class="user-profile">
    <div class="header">
      <h1></h1>
      <div class="header-buttons">
        <el-button type="default" size="small" icon="el-icon-back" @click="goBack"></el-button>
        <el-button type="primary" size="small" icon="el-icon-s-home" @click="goHome"></el-button>
      </div>
    </div>

    <!--  -->
    <div class="search-area">
      <el-form :inline="true" :model="queryParams" class="search-form">
        <el-form-item label="">
          <el-input v-model="queryParams.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="queryParams.departmentId" placeholder="" clearable>
            <el-option v-for="dept in departments" :key="dept.id" :label="dept.name" :value="dept.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="queryParams.role" placeholder="" clearable>
            <el-option label="" value="SUPER_ADMIN"></el-option>
            <el-option label="" value="NORMAL_USER"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="queryParams.dateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleQuery"></el-button>
          <el-button @click="resetQuery"></el-button>
          <el-button type="primary" icon="el-icon-plus" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--  -->
    <el-table
      :data="tableData"
      v-loading="loading"
      border
      style="width: 100%">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column prop="departmentName" label="">
        <template slot-scope="scope">
          {{ getDepartmentName(scope.row.departmentId) }}
        </template>
      </el-table-column>
      <el-table-column prop="role" label="">
        <template slot-scope="scope">
          <el-tag :type="scope.row.role === 'SUPER_ADMIN' ? 'danger' : ''">
            {{ scope.row.role === 'SUPER_ADMIN' ? '' : '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="createdTime" label=""></el-table-column>
      <el-table-column label="" width="150" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <span class="total-count"> {{ total }} </span>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryParams.pageNum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryParams.pageSize"
        layout="prev, pager, next, jumper"
        :total="total">
      </el-pagination>
      <span class="page-info"> {{ queryParams.pageNum }} </span>
    </div>

    <!--  -->
    <el-dialog
      :title="dialogType === 'add' ? '' : ''"
      :visible.sync="dialogVisible"
      width="500px"
      @closed="resetForm">
      <el-form
        ref="form"
        :model="formData"
        :rules="rules"
        label-width="80px">
        <el-form-item label="" prop="username">
          <el-input v-model="formData.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" v-if="dialogType === 'add'">
          <el-input v-model="formData.password" type="password" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="departmentId">
          <el-select v-model="formData.departmentId" placeholder="">
            <el-option v-for="dept in departments" :key="dept.id" :label="dept.name" :value="dept.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="role">
          <el-select v-model="formData.role" placeholder="">
            <el-option label="" value="NORMAL_USER"></el-option>
            <el-option label="" value="SUPER_ADMIN"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="submitForm"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="deleteDialogVisible"
      width="300px">
      <div class="delete-confirm">
        <i class="el-icon-warning"></i>
        <span></span>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="deleteDialogVisible = false"></el-button>
        <el-button type="primary" @click="confirmDelete"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getUserList, createUser, updateUser, deleteUser } from '@/api/user'
import { getDepartmentList } from '@/api/department'

export default {
  name: 'UserProfile',
  data() {
    return {
      // 
      queryParams: {
        pageNum: 1,
        pageSize: 10,
        username: '',
        departmentId: undefined,
        role: '',
        dateRange: []
      },
      // 
      tableData: [],
      loading: false,
      total: 0,

      // 
      departments: [],

      // 
      dialogVisible: false,
      dialogType: 'add',
      deleteDialogVisible: false,
      deleteUserId: null,

      // 
      formData: {
        username: '',
        password: '',
        departmentId: undefined,
        role: 'NORMAL_USER'
      },

      // 
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
        departmentId: [
          { required: true, message: '', trigger: 'change' }
        ],
        role: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    goBack() {
      if (this.$route.path !== '/admin') {
        this.$router.push('/admin')
      }
    },
    goHome() {
      if (this.$route.path !== '/') {
        this.$router.push('/')
      }
    },
    // 
    async fetchDepartments() {
      try {
        const res = await getDepartmentList()
        this.departments = Array.isArray(res.data) ? res.data : []
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    async fetchUserList() {
      this.loading = true
      try {
        const res = await getUserList(this.queryParams)
        this.tableData = res.list || []
        this.total = res.total || 0
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },

    // 
    handleQuery() {
      this.queryParams.pageNum = 1
      this.fetchUserList()
    },

    // 
    resetQuery() {
      this.queryParams = {
        pageNum: 1,
        pageSize: 10,
        username: '',
        departmentId: undefined,
        role: '',
        dateRange: []
      }
      this.fetchUserList()
    },

    // 
    handleSizeChange(val) {
      this.queryParams.pageSize = val
      this.fetchUserList()
    },

    handleCurrentChange(val) {
      this.queryParams.pageNum = val
      this.fetchUserList()
    },

    // 
    handleAdd() {
      this.dialogType = 'add'
      this.formData = {
        username: '',
        password: '',
        departmentId: undefined,
        role: 'NORMAL_USER'
      }
      this.dialogVisible = true
    },

    // 
    handleEdit(row) {
      this.dialogType = 'edit'
      this.formData = {
        id: row.id,
        username: row.username,
        departmentId: row.departmentId,
        role: row.role
      }
      this.dialogVisible = true
    },

    // 
    handleDelete(row) {
      this.deleteUserId = row.id
      this.deleteDialogVisible = true
    },

    // 
    async confirmDelete() {
      try {
        await deleteUser(this.deleteUserId)
        this.$message.success('')
        this.deleteDialogVisible = false
        this.fetchUserList()
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    async submitForm() {
      try {
        await this.$refs.form.validate()
        
        if (this.dialogType === 'add') {
          await createUser(this.formData)
          this.$message.success('')
        } else {
          const { id, ...updateData } = this.formData
          await updateUser(id, updateData)
          this.$message.success('')
        }
        
        this.dialogVisible = false
        this.fetchUserList()
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    // 
    resetForm() {
      if (this.$refs.form) {
        this.$refs.form.resetFields()
      }
      this.formData = {
        username: '',
        password: '',
        departmentId: undefined,
        role: 'NORMAL_USER'
      }
    },

    // 
    getDepartmentName(departmentId) {
      if (!departmentId) return ''
      const department = this.departments.find(d => d.id === departmentId)
      return department ? department.name : ''
    }
  },
  created() {
    this.fetchDepartments()
    this.fetchUserList()
  }
}
</script>

<style scoped>
.user-profile {
  padding: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header h1 {
  font-size: 24px;
  color: #333;
  margin: 0;
}

.header-buttons {
  display: flex;
  gap: 10px;
}

.search-area {
  background-color: #fff;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.search-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.pagination-container {
  margin-top: 20px;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  border-radius: 4px;
}

.total-count {
  color: #606266;
  font-size: 13px;
}

.page-info {
  color: #606266;
  font-size: 13px;
}

.delete-confirm {
  text-align: center;
  padding: 20px 0;
}

.delete-confirm i {
  font-size: 24px;
  color: #e6a23c;
  margin-right: 10px;
}

.delete-confirm span {
  font-size: 16px;
  color: #606266;
}

@media screen and (max-width: 768px) {
  .search-form {
    flex-direction: column;
  }

  .search-form .el-form-item {
    margin-right: 0;
  }

  .pagination-container {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }
}
</style> 