<template>
  <div>
    <app-layout :sidebar-active="sidebarActive" active-tab="chat" sidebar-title="" new-button-text=""
      @toggle-sidebar="toggleSidebar" @create-new-chat="createNewChat" @command="handleCommand" @share="shareHistory">
      <template v-slot:sidebar-content>
        <div v-for="(chat, index) in chatHistoryList" :key="index" class="history-item"
          :class="{ active: currentChatIndex === index }" @click="switchChat(index)">
          <div class="history-item-content">
            <i class="el-icon-chat-dot-round"></i>
            <span class="history-title">{{ chat.title }}</span>
          </div>
          <el-dropdown trigger="click" @command="(cmd) => handleChatAction(cmd, index)" @click.stop>
            <i class="el-icon-more"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="rename"></el-dropdown-item>
              <el-dropdown-item command="share"></el-dropdown-item>
              <el-dropdown-item command="delete"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </template>

      <!--  -->
      <div class="chat-history" ref="chatHistory">
        <div v-if="currentChat.messages.length === 0" class="empty-chat-container">
          <div class="greeting-section">
            <div class="greeting-header">
              <img src="../assets/back.png" alt="Logo" class="logo-image" />
              <h1 class="greeting-title">Hi~ </h1>
            </div>
            <p class="greeting-subtitle">
              ~
            </p>
          </div>

          <!--  -  -->
          <div class="prompt-section">
            <div class="prompt-title"></div>
            <div class="prompt-list">
              <div class="prompt-item" @click="selectPrompt('20')">
                20
              </div>
              <div class="prompt-item" @click="selectPrompt('')">
                
              </div>
              <div class="prompt-item" @click="
                  selectPrompt('')
                ">
                
              </div>
            </div>
          </div>

          <!--  -->
          <div class="input-box">
            <div class="input-area-centered">
              <div class="model-dropdown">
                <el-dropdown trigger="click" @command="changeModel">
                  <div class="model-name-display">
                    <i class="el-icon-cpu"></i>
                    {{ currentModel }}
                    <i class="el-icon-arrow-down"></i>
                  </div>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="GPT-4o">GPT-4o</el-dropdown-item>
                    <el-dropdown-item command="deepseek-v3">deepseek-v3</el-dropdown-item>
                    <el-dropdown-item command="Qwen2.5-Max">Qwen2.5-Max</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
              <el-input type="textarea" :rows="1" placeholder=" DeepSeek " v-model="userInput" class="chat-input"
                resize="none" @keydown.enter.native.prevent="sendMessage">
              </el-input>
              <div class="input-actions">
                <el-button class="action-btn" icon="el-icon-link" circle size="small" @click="uploadFile"></el-button>
                <el-button class="action-btn" icon="el-icon-picture-outline" circle size="small"
                  @click="uploadImage"></el-button>
                <el-button class="action-btn" icon="el-icon-microphone" circle size="small"
                  @click="startVoiceInput"></el-button>
                <el-button type="primary" icon="el-icon-s-promotion" class="send-button" :disabled="!userInput.trim()"
                  @click="sendMessage"></el-button>
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="chat-messages" v-if="currentChat.messages.length > 0">
          <div v-for="(message, index) in currentChat.messages" :key="index" :class="[
              'message',
              message.role === 'user' ? 'user-message' : 'assistant-message',
          ]">
            <div class="message-avatar" v-if="message.role === 'assistant'">
              <img src="../assets/ai.png" alt="AI" class="avatar-image" />
            </div>
            <div class="message-avatar user-avatar-container" v-else>
              <div class="user-message-avatar">{{ userInitial }}</div>
            </div>
            <div class="message-bubble">
              <div class="message-header" v-if="message.role === 'assistant'">
                <div class="model-info">
                  <span class="model-name">{{ message.model || currentModel }}</span>
                  <span class="response-time">{{ message.time || new Date().toLocaleString('zh-CN', { hour12: false })
                  }}</span>
                </div>
              </div>
              <div class="message-header" v-else>
                <div class="user-info">
                  <span class="send-time">{{ message.time || new Date().toLocaleString('zh-CN', { hour12: false })
                  }}</span>
                </div>
              </div>
              <div class="message-content"
                v-if="message.role === 'user' || (!message.imageUrl && message.role === 'assistant')">
                {{ message.content }}
              </div>
              <div class="image-content" v-if="message.imageUrl && message.role === 'assistant'">
                <div class="message-text">{{ message.content }}</div>
                <img :src="message.imageUrl" alt="Generated Image" class="generated-image" @load="scrollToBottom"
                  @error="handleImageError" />
              </div>
              <div class="message-actions" v-if="message.role === 'assistant'">
                <template v-if="message.imageUrl">
                  <i class="el-icon-star-off" :class="{ 'liked': message.liked }" title="" @click="likeMessage(message)"></i>
                  <i class="el-icon-download" title="" @click="downloadImage(message)"></i>
                  <i class="el-icon-delete" title="" @click="deleteMessage(index)"></i>
                  <i class="el-icon-refresh" title="" @click="regenerateMessage(index)"></i>
                </template>
                <template v-else>
                  <i class="el-icon-document-copy" title="" @click="copyMessage(message)"></i>
                  <i class="el-icon-delete" title="" @click="deleteMessage(index)"></i>
                  <i class="el-icon-refresh" title="" @click="regenerateMessage(index)"></i>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--  () -->
      <div class="footer-input-container" v-if="currentChat.messages.length > 0">
        <div class="input-area-bottom">
          <div class="model-dropdown">
            <el-dropdown trigger="click" @command="changeModel">
              <div class="model-name-display">
                <i class="el-icon-cpu"></i>
                {{ currentModel }}
                <i class="el-icon-arrow-down"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="GPT-4o">GPT-4o</el-dropdown-item>
                <el-dropdown-item command="deepseek-v3">deepseek-v3</el-dropdown-item>
                <el-dropdown-item command="Qwen2.5-Max">Qwen2.5-Max</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <el-input type="textarea" :rows="1" placeholder=" DeepSeek " v-model="userInput" class="chat-input"
            resize="none" @keydown.enter.native.prevent="sendMessage">
          </el-input>
          <div class="input-actions">
            <el-button class="action-btn" icon="el-icon-link" circle size="small" @click="uploadFile"></el-button>
            <el-button class="action-btn" icon="el-icon-picture-outline" circle size="small"
              @click="uploadImage"></el-button>
            <el-button class="action-btn" icon="el-icon-microphone" circle size="small"
              @click="startVoiceInput"></el-button>
            <el-button type="primary" icon="el-icon-s-promotion" class="send-button" :disabled="!userInput.trim()"
              @click="sendMessage"></el-button>
          </div>
        </div>
      </div>
    </app-layout>

    <!--  -->
    <user-preferences :visible.sync="preferencesVisible" @save="savePreferences" />

    <!--  -->
    <share-dialog :visible.sync="shareDialogVisible" :share-link="shareLink" @close="shareDialogVisible = false" />
    
    <!--  -->
    <account-modal :visible.sync="accountModalVisible" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import AppLayout from "@/layout/AppLayout.vue";
import UserPreferences from "@/components/UserPreferences.vue";
import ShareDialog from "@/components/ShareDialog.vue";
import AccountModal from "@/components/AccountModal.vue";
import { sendMessageToBackend } from '@/api/chat';

export default {
  name: "ChatView",
  components: {
    AppLayout,
    UserPreferences,
    ShareDialog,
    AccountModal
  },
  data() {
    return {
      userInput: "",
      currentChatIndex: 0,
      sidebarActive: true, // 
      preferencesVisible: false,
      accountModalVisible: false,
      inputExpanded: false,
      shareDialogVisible: false,
      shareLink: "",
      selectedStyle: "",
      currentModel: "GPT-4o", // 
      chatHistoryList: [
        {
          id: 1,
          title: "",
          messages: [
            { role: "user", content: "" },
            {
              role: "assistant",
              content:
                "",
              model: "GPT-4o",
              time: "2024-06-01 10:15"
            },
          ],
        },
        {
          id: 2,
          title: "Python",
          messages: [
            { role: "user", content: "PythonJSON" },
            {
              role: "assistant",
              content:
                "PythonJSONjsonjson.loads()JSONPythonjson.dumps()PythonJSONjson.load()JSONjson.dump()PythonJSON",
              model: "GPT-4o",
              time: "2024-06-01 11:30"
            },
          ],
        },
        {
          id: 3,
          title: "",
          messages: [
            { role: "user", content: "" },
            {
              role: "assistant",
              content: "",
              imageUrl: "https://example.com/cat_image.jpg", // URL
              model: "GPT-4o",
              time: "2024-06-02 14:25"
            },
          ],
        },
        {
          id: 4,
          title: "",
          messages: [],
        },
      ],
      styles: [
        { value: '', label: '' },
        { value: '', label: '' },
        { value: '', label: '' },
        { value: '', label: '' },
        { value: '', label: '' },
      ],
    };
  },
  computed: {
    ...mapGetters([
      "availableModels",
      "currentUser",
      "isAdmin",
    ]),
    userInitial() {
      return this.currentUser
        ? this.currentUser.username.charAt(0).toUpperCase()
        : "?";
    },
    currentChat() {
      return (
        this.chatHistoryList[this.currentChatIndex] || {
          id: 0,
          title: "",
          messages: [],
        }
      );
    },
    isMobile() {
      return window.innerWidth <= 767;
    },
    isImageModel() {
      // false
      return false;
    }
  },
  methods: {
    ...mapActions(["logout", "toggleAccountModal"]),

    async sendMessage() {
      if (!this.userInput.trim()) return;

      // 10
      if (this.currentChat.messages.length === 0) {
        this.currentChat.title =
          this.userInput.substring(0, 10) +
          (this.userInput.length > 10 ? "..." : "");
      }

      // 
      this.currentChat.messages.push({
        role: "user",
        content: this.userInput,
        time: new Date().toLocaleString('zh-CN', { hour12: false })
      });

      const userMessage = this.userInput;
      this.userInput = ""; // 

      try {
        // API
        const response = await sendMessageToBackend(userMessage);

        // HTML
        const isImage = response.includes('<img');

        if (isImage) {
          // HTMLURL
          const urlMatch = response.match(/src='([^']*)'/) || response.match(/src="([^"]*)"/);
          const imageUrl = urlMatch ? urlMatch[1] : null;

          // AI
          this.currentChat.messages.push({
            role: "assistant",
            content: "",
            imageUrl: imageUrl,
            model: this.currentModel,
            time: new Date().toLocaleString('zh-CN', { hour12: false })
          });
        } else {
          // 
          this.currentChat.messages.push({
            role: "assistant",
            content: response,
            model: this.currentModel,
            time: new Date().toLocaleString('zh-CN', { hour12: false })
          });
        }

        // 
        this.$nextTick(() => {
          const chatHistory = this.$refs.chatHistory;
          if (chatHistory) {
            chatHistory.scrollTop = chatHistory.scrollHeight;
          }
        });
      } catch (error) {
        // 
        this.currentChat.messages.push({
          role: "assistant",
          content: `: ${error.message}`,
          model: this.currentModel,
          time: new Date().toLocaleString('zh-CN', { hour12: false })
        });

        // 
        this.$message.error(error.message);

        console.error(':', error);
      }
    },
    selectPrompt(prompt) {
      this.userInput = prompt;
    },
    handleCommand(command) {
      if (command === "profile") {
        this.accountModalVisible = true;
      } else if (command === "logout") {
        this.$store.dispatch("logout");
        this.$router.push("/login");
      } else if (command === "admin") {
        this.$router.push("/admin");
      } else if (command === "preferences") {
        this.preferencesVisible = true;
      }
    },
    switchChat(index) {
      this.currentChatIndex = index;
      // 
      this.$nextTick(() => {
        const chatHistory = this.$refs.chatHistory;
        if (chatHistory && this.currentChat.messages.length > 0) {
          chatHistory.scrollTop = chatHistory.scrollHeight;
        }
      });

      // 
      if (this.isMobile) {
        this.sidebarActive = false;
      }
    },
    createNewChat() {
      this.chatHistoryList.unshift({
        id: Date.now(),
        title: "",
        messages: [],
      });
      this.currentChatIndex = 0;

      // 
      if (this.isMobile) {
        this.sidebarActive = false;
      }
    },
    handleChatAction(cmd, index) {
      if (cmd === "rename") {
        this.$prompt("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          inputValue: this.chatHistoryList[index].title,
        })
          .then(({ value }) => {
            if (value) {
              this.chatHistoryList[index].title = value;
              this.$message({
                type: "success",
                message: "",
              });
            }
          })
          .catch(() => { });
      } else if (cmd === "share") {
        // 
        this.exportChat(index);
      } else if (cmd === "delete") {
        this.$confirm("?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        })
          .then(() => {
            this.chatHistoryList.splice(index, 1);
            if (this.chatHistoryList.length === 0) {
              this.chatHistoryList.push({
                id: Date.now(),
                title: "",
                messages: [],
              });
            }
            if (index === this.currentChatIndex) {
              this.currentChatIndex = 0;
            } else if (index < this.currentChatIndex) {
              this.currentChatIndex--;
            }
            this.$message({
              type: "success",
              message: "!",
            });
          })
          .catch(() => { });
      }
    },
    toggleSidebar() {
      this.sidebarActive = !this.sidebarActive;
    },
    toggleInputExpanded() {
      this.inputExpanded = !this.inputExpanded;
    },
    exportChat(index) {
      const chat = this.chatHistoryList[index];
      // 
      const content = JSON.stringify(chat, null, 2);
      const blob = new Blob([content], { type: "application/json" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `${chat.title}.json`;
      a.click();
      URL.revokeObjectURL(url);
    },
    shareHistory() {
      if (this.currentChat.messages.length === 0) {
        this.$message.warning("");
        return;
      }
      // 
      this.shareLink = `https://chat.example.com/share/${this.currentChat.id}`;
      this.shareDialogVisible = true;
    },
    savePreferences(preferences) {
      // 
      console.log(":", preferences);
      this.$message.success("");
    },
    likeMessage(message) {
      // 
      message.liked = !message.liked;
      
      // 
      if (message.imageUrl && message.liked) {
        this.saveImageToAdmin(message);
        this.$message.success("");
      } else if (message.liked) {
        this.$message.success("");
      } else {
        this.$message.info("");
      }
    },
    saveImageToAdmin(message) {
      // API
      // 
      console.log('', message.imageUrl);
      // API
      // this.$http.post('/api/admin/save-image', {
      //   url: message.imageUrl,
      //   content: message.content,
      //   time: message.time
      // });
    },
    downloadImage(message) {
      if (message.imageUrl) {
        const a = document.createElement("a");
        a.href = message.imageUrl;
        a.download = `image_${Date.now()}.jpg`;
        a.click();
      }
    },
    deleteMessage(index) {
      // 
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.currentChat.messages.splice(index, 1);
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => { });
    },
    regenerateMessage(index) {
      // 
      if (index > 0 && this.currentChat.messages[index].role === 'assistant') {
        // 
        this.currentChat.messages.splice(index, 1);
        
        // 
        setTimeout(() => {
          const prevMessage = this.currentChat.messages[index - 1];
          // 
          this.currentChat.messages.splice(index, 0, {
            role: "assistant",
            content: `"${prevMessage.content}"`,
            model: this.currentModel,
            time: new Date().toLocaleString('zh-CN', { hour12: false })
          });
        }, 500);
      }
    },
    uploadFile() {
      // 
      this.$message.info("");
    },
    uploadImage() {
      // 
      this.$message.info("");
    },
    startVoiceInput() {
      // 
      this.$message.info("");
    },
    scrollToBottom() {
      this.$nextTick(() => {
        const chatHistory = this.$refs.chatHistory;
        if (chatHistory) {
          chatHistory.scrollTop = chatHistory.scrollHeight;
        }
      });
    },
    handleImageError(e) {
      console.error(':', e);
      e.target.style.display = 'none';
      this.$message.error('');
    },
    changeModel(model) {
      this.currentModel = model;
      this.$message.success(` ${model} `);
    },
    copyMessage(message) {
      navigator.clipboard.writeText(message.content)
        .then(() => {
          this.$message.success("");
        })
        .catch(err => {
          this.$message.error(": " + err);
        });
    }
  },
  mounted() {
    // 
    window.addEventListener("resize", () => {
      if (this.isMobile && this.sidebarActive) {
        this.sidebarActive = false;
      }
    });
  }
};
</script>

