<template>
  <el-dialog 
    :visible.sync="localVisible" 
    width="500px"
    class="account-modal"
    :show-close="true"
    append-to-body
    title=""
    custom-class="account-dialog-custom"
  >
    <div v-if="!isEditing" class="user-profile-info">
      <el-avatar :size="80" class="user-avatar">{{ userInitial }}</el-avatar>
      <h3 class="username">{{ currentUser.username || '' }}</h3>
      
      <div class="info-items">
        <div class="info-item">
          <span class="info-label"></span>
          <span class="info-value"></span>
        </div>
        <div class="info-item">
          <span class="info-label"></span>
          <span class="info-value">{{ currentUser.department || '' }}</span>
        </div>
        <div class="info-item">
          <span class="info-label"></span>
          <span class="info-value">
            <el-tag type="primary" size="small" v-if="isAdmin"></el-tag>
            <el-tag size="small" v-else></el-tag>
          </span>
        </div>
        <div class="info-item">
          <span class="info-label"></span>
          <span class="info-value">{{ currentUser.createdAt || '2024-01-01 12:00:00' }}</span>
        </div>
      </div>

      <div class="account-actions">
        <el-button type="primary" icon="el-icon-edit" @click="startEditing"></el-button>
        <el-button type="danger" icon="el-icon-switch-button" @click="logout"></el-button>
      </div>
    </div>

    <!--  -->
    <el-form 
      v-else 
      :model="userForm" 
      :rules="rules" 
      ref="userForm" 
      label-width="80px" 
      class="edit-form"
    >
      <el-form-item label="" prop="username">
        <el-input v-model="userForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="userForm.password" placeholder="" show-password></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword" v-if="userForm.password">
        <el-input type="password" v-model="userForm.confirmPassword" show-password></el-input>
      </el-form-item>
      <el-form-item label="" prop="department">
        <el-select v-model="userForm.department" placeholder="">
          <el-option
            v-for="dept in departments"
            :key="dept.id"
            :label="dept.name"
            :value="dept.id"
          ></el-option>
        </el-select>
      </el-form-item>
      
      <div class="form-actions">
        <el-button @click="cancelEdit"></el-button>
        <el-button type="primary" @click="saveUserInfo"></el-button>
      </div>
    </el-form>
  </el-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'AccountModal',
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    // 
    const validatePass = (rule, value, callback) => {
      if (value !== this.userForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    
    return {
      localVisible: this.visible,
      isEditing: false,
      departments: [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' },
        { id: 5, name: '' }
      ],
      userForm: {
        username: '',
        password: '',
        confirmPassword: '',
        department: null
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
        ],
        password: [
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { validator: validatePass, trigger: 'blur' }
        ],
        department: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([
      'currentUser',
      'isAdmin'
    ]),
    userInitial() {
      return this.currentUser && this.currentUser.username
        ? this.currentUser.username.charAt(0).toUpperCase()
        : '?';
    }
  },
  watch: {
    visible(val) {
      this.localVisible = val;
      if (val) {
        this.isEditing = false;
      }
    },
    localVisible(val) {
      this.$emit('update:visible', val);
      if (!val) {
        this.isEditing = false;
      }
    },
    currentUser: {
      handler(newVal) {
        if (newVal) {
          this.userForm.username = newVal.username || '';
          this.userForm.department = newVal.departmentId || null;
        }
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions(['logout', 'updateUserProfile']),
    startEditing() {
      // 
      this.userForm = {
        username: this.currentUser.username || '',
        password: '',
        confirmPassword: '',
        department: this.currentUser.departmentId || null
      };
      this.isEditing = true;
    },
    cancelEdit() {
      this.isEditing = false;
    },
    saveUserInfo() {
      this.$refs.userForm.validate(valid => {
        if (valid) {
          // API
          const updatedUser = {
            ...this.currentUser,
            username: this.userForm.username,
            departmentId: this.userForm.department
          };
          
          // 
          if (this.userForm.password) {
            updatedUser.password = this.userForm.password;
          }
          
          // 
          this.updateUserProfile(updatedUser)
            .then(() => {
              this.$message.success('');
              this.isEditing = false;
            })
            .catch(err => {
              this.$message.error(': ' + err.message);
            });
        } else {
          return false;
        }
      });
    }
  }
}
</script>

