<template>
  <el-dialog
    title=""
    :visible.sync="localVisible"
    width="500px"
    class="share-dialog"
  >
    <div class="share-content">
      <p class="share-text">: {{ shareLink }}</p>
      <el-button type="primary" @click="copyLink"></el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'ShareDialog',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    shareLink: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      localVisible: this.visible
    }
  },
  watch: {
    visible(val) {
      this.localVisible = val;
    },
    localVisible(val) {
      this.$emit('update:visible', val);
      if (!val) {
        this.$emit('close');
      }
    }
  },
  methods: {
    copyLink() {
      // 
      const input = document.createElement('input');
      input.value = this.shareLink;
      document.body.appendChild(input);
      input.select();
      document.execCommand('copy');
      document.body.removeChild(input);
      
      this.$message({
        message: '',
        type: 'success',
        duration: 2000
      });
    }
  }
}
</script>

<style scoped>
.share-dialog {
  text-align: center;
}

.share-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 10px;
}

.share-text {
  font-size: 16px;
  word-break: break-all;
  max-width: 100%;
  margin: 0;
}
</style> 