import request from '@/utils/request'

/**
 * 
 */
export function getChatSessions() {
  return request({
    url: '/api/chat/sessions',
    method: 'get'
  })
}

/**
 * 
 * @param {String} modelType 
 */
export function createChatSession(modelType) {
  return request({
    url: '/api/chat/session',
    method: 'post',
    params: { modelType }
  })
}

/**
 * 
 * @param {Number} sessionId ID
 */
export function getChatMessages(sessionId) {
  return request({
    url: `/api/chat/messages/${sessionId}`,
    method: 'get'
  })
}

/**
 * 
 * @param {Number} sessionId ID
 */
export function deleteChatSession(sessionId) {
  return request({
    url: `/api/chat/session/${sessionId}`,
    method: 'delete'
  })
}

/**
 * 
 * @param {Number} sessionId ID
 * @param {String} title 
 */
export function renameChatSession(sessionId, title) {
  return request({
    url: `/api/chat/session/${sessionId}/title`,
    method: 'put',
    data: { title }
  })
}

/**
 * ()
 * @param {Object} data sessionIdmessage
 */
export function sendChatMessage(data) {
  return request({
    url: '/api/chat/send',
    method: 'post',
    data
  })
}

/**
 * ()
 * @param {String} prompt 
 * @returns {Promise<String>} 
 */
export async function sendMessageToBackend(prompt) {
  const baseUrl = process.env.VUE_APP_BASE_API || 'http://localhost:5000';
  const response = await fetch(`${baseUrl}/chat?prompt=${encodeURIComponent(prompt)}`, {
    method: 'GET',
    headers: {
      'Accept': '*/*',
    },
    mode: 'cors',
    credentials: 'omit',
  });

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  return await response.text();
}

/**
 * 
 * @param {Object} data sessionIdmessagemodelType
 */
export function initStreamResponse(data) {
  return request({
    url: '/api/chat/stream',
    method: 'post',
    data
  })
}

/**
 * 
 */
export function getModelTypes() {
  return request({
    url: '/api/chat/models',
    method: 'get'
  })
}

/**
 * EventSource
 * @param {Number} sessionId ID
 * @returns {EventSource} EventSource
 */
export function createStreamConnection(sessionId) {
  const baseUrl = process.env.VUE_APP_BASE_API || 'http://localhost:5000'
  return new EventSource(`${baseUrl}/api/chat/stream/${sessionId}`)
} 